import React from 'react';

const StyledHeading = ({ children, level = 2 }) => {
  const Tag = `h${level}`;

  return (
    <Tag className="text-3xl sm:text-4xl md:text-5xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-yellow-500 to-teal-400 uppercase tracking-widest text-center">
      {children}
    </Tag>
  );
};

export default StyledHeading;

